						      showNewMessageTip: false,      // ÊòØÂê¶ÊòæÁ§∫Êñ∞Ê∂àÊÅØÊèêÁ§∫
						      newMessageCount: 0,             // Êñ∞Ê∂àÊÅØÊï∞Èáè
						      scrollTop: 0,                   // ÂΩìÂâçÊªöÂä®‰ΩçÁΩÆ
						      scrollThreshold: 100,           // ÈòàÂÄº(rpx)ÔºåË∑ùÁ¶ªÂ∫ïÈÉ®Â§öÂ∞ëÊó∂ÊòæÁ§∫ÊèêÁ§∫
						      isNearBottom: true,             // ÊòØÂê¶Êé•ËøëÂ∫ïÈÉ®
						      lastMessagesLength: 0,          // ‰∏ä‰∏ÄÊ¨°ÁöÑÊ∂àÊÅØÊï∞Èáè
							  scrollViewHeight: 0,
							
			};
		},
		async onLoad(options) {
			const queryObj = {code: this.openid};
