					const chatData  = JSON.parse(decodeURIComponent(options.goods_info)) || null;
					// Âà§Êñ≠ÊòØÂê¶‰º†‰∫Ü other_openid
					if (chatData.other_openid) {
						// Êé•Êî∂ÂÆåÊï¥Êï∞ÊçÆ(ÂåÖÂê´ other_openid)
						this.goods_info = chatData;
						this.otherOpenid = chatData.other_openid;
						console.log('Êé•Êî∂Âà∞ other_openid:', this.otherOpenid);
						const queryObj1 = {code: chatData.other_openid};
						const { data: res1 } = await uni.$http.get('/users/userinfo', queryObj1);
						if (res1.meta.status === 200) {
							this.other_nickname = res1.message.nickname
							this.other_avatarUrl = res1.message.avatarUrl
						}
					} else {
						// Âè™Êé•Êî∂ goods_info
						this.goods_info = chatData;
						console.log('Âè™Êé•Êî∂ goods_info');
					}
				}
				
			} catch (e) {
				console.warn('publisher Ëß£ÊûêÂ§±Ë¥•:', e)
				this.goods_info = null
			}
			
			// ÂàùÂßãÂåñÂä†ËΩΩÊ∂àÊÅØ
			await this.loadMessages();
			
			// üî• Êñ∞Â¢ûÔºöÂàùÂßãÂåñÊ∂àÊÅØÊï∞Èáè
			  this.lastMessagesLength = this.messages.length;
			  
			// Âä†ËΩΩÂΩìÂâçÂïÜÂìÅÁöÑËÆ¢ÂçïÁä∂ÊÄÅ
			await this.loadCurrentOrder();
			
			// ÂêØÂä®ËΩÆËØ¢
			this.startPolling();
			
			// Âä†ËΩΩÊú¨Âú∞‰øùÂ≠òÁöÑÂú∞ÂùÄ
			this.loadLocalAddress();
			
			// üî• Êñ∞Â¢ûÔºöÁ°Æ‰øùÂàùÂßã‰ΩçÁΩÆÂú®Â∫ïÈÉ®
			  this.$nextTick(() => {
			    this.scrollToBottom();
			    this.isNearBottom = true;
			  });
		},
		
		// È°µÈù¢ÈöêËóèÊó∂ÂÅúÊ≠¢ËΩÆËØ¢
		onHide() {
