			// ÈáçÊñ∞Âä†ËΩΩÊú¨Âú∞Âú∞ÂùÄ(ÂèØËÉΩÂú®ÂÖ∂‰ªñÈ°µÈù¢Ë¢´‰øÆÊîπ)
			await this.loadLocalAddress();
