		// È°µÈù¢Âç∏ËΩΩÊó∂ÂÅúÊ≠¢ËΩÆËØ¢
		onUnload() {
			this.stopPolling();
		},
		
		methods: {
			...mapMutations('m_user', ['updateUserInfo', 'updateToken', 'updateUserBase', 'updateAddress']),
			
			// ÂÆåÊï¥ÁöÑ handleScroll
			handleScroll(e) {
			    const { scrollTop, scrollHeight, deltaY } = e.detail;
			    this.scrollTop = scrollTop;
			    
			    // ËÆ°ÁÆóË∑ùÁ¶ªÂ∫ïÈÉ®ÁöÑË∑ùÁ¶ª
			    let distanceToBottom = 0;
			    if (this.scrollViewHeight > 0) {
			        distanceToBottom = scrollHeight - scrollTop - this.scrollViewHeight;
			        this.isNearBottom = distanceToBottom < this.scrollThreshold;
			    } else {
			        // ÈôçÁ∫ßÊñπÊ°àÔºö‰ΩøÁî®ÁôæÂàÜÊØî
			        const scrollPercentage = scrollTop / scrollHeight;
			        this.isNearBottom = scrollPercentage > 0.95;
			    }
			    
			    console.log('üìä', {
			        scrollTop: scrollTop.toFixed(0),
			        scrollHeight,
			        viewHeight: this.scrollViewHeight,
			        distanceToBottom: distanceToBottom.toFixed(0),
			        isNearBottom: this.isNearBottom
			    });
			    
			    // ÊªöÂä®Âà∞Â∫ïÈÉ®Êó∂Ê∏ÖÈô§ÊèêÁ§∫
			    if (this.isNearBottom && this.showNewMessageTip) {
			        this.showNewMessageTip = false;
			        this.newMessageCount = 0;
			    }
			},
			  
			  /**
			   * üî• Ëß¶Â∫ï‰∫ã‰ª∂
			   */
			  handleScrollToLower() {
			    this.isNearBottom = true;
			    if (this.showNewMessageTip) {
			      this.showNewMessageTip = false;
			      this.newMessageCount = 0;
			    }
			  },
			  
			  /**
			   * üî• ÁÇπÂáªÊñ∞Ê∂àÊÅØÊèêÁ§∫Ë∑≥ËΩ¨Âà∞Â∫ïÈÉ®
			   */
			  scrollToBottomWithTip() {
			    this.showNewMessageTip = false;
			    this.newMessageCount = 0;
			    this.isNearBottom = true;
			    
			    this.$nextTick(() => {
			      this.scrollToBottom();
			    });
			  },
			  
			/**
			         * üî• Êñ∞Â¢ûÔºöÊ£ÄÊü•‰∫§ÊòìÊòØÂê¶ËøáÊúü
			         */
			        async checkTransactionExpiration() {
			            try {
			                // Êü•ÊâæÊúÄÂêé‰∏ÄÊù°‰∫§ÊòìÊ∂àÊÅØ
			                const lastTransactionMsg = [...this.messages]
			                    .reverse()
			                    .find(msg => msg.message_type === 'transaction');
			                
			                if (!lastTransactionMsg) {
			                    return;
			                }
			                
			                // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÊúâÂêåÊÑè„ÄÅÂÆåÊàêÊàñÁ≥ªÁªüÂÖ≥Èó≠Ê∂àÊÅØ
			                const hasAgreeMsg = this.messages.some(msg => msg.message_type === 'agree');
			                const hasFinishMsg = this.messages.some(msg => msg.message_type === 'finish');
			                const hasSystemCloseMsg = this.messages.some(msg => 
			                    msg.message_type === 'system' && 
			                    msg.content && 
			                    msg.content.includes('‰∫§ÊòìËá™Âä®ÂÖ≥Èó≠')
			                );
			                
			                // Â¶ÇÊûúÂ∑≤ÁªèÊúâÂêéÁª≠Êìç‰ΩúÔºå‰∏çÈúÄË¶ÅÊ£ÄÊü•ËøáÊúü
			                if (hasAgreeMsg || hasFinishMsg || hasSystemCloseMsg) {
			                    return;
			                }
			                
			                // Ëß£Êûê‰∫§ÊòìÊ∂àÊÅØÊó∂Èó¥Êà≥ÔºàÁßíËΩ¨ÊØ´ÁßíÔºâ
			                const transactionTime = lastTransactionMsg.created_at * 1000;
			                const now = Date.now();
			                const timePassed = now - transactionTime;
			                
			                // üî• Â¶ÇÊûúË∂ÖËøá7Â§©ÔºåÂèëÈÄÅÁ≥ªÁªüÊ∂àÊÅØÂÖ≥Èó≠‰∫§Êòì
			                if (timePassed > this.TRANSACTION_EXPIRE_TIME) {
			                    console.log('‚è∞ ‰∫§ÊòìÂ∑≤ËøáÊúüÔºåËá™Âä®ÂÖ≥Èó≠');
			                    await this.sendSystemCloseMessage();
			                }
			            } catch (error) {
			                console.error('‚ùå Ê£ÄÊü•‰∫§ÊòìËøáÊúüÂ§±Ë¥•:', error);
			            }
			        },
			        
			        /**
			         * üî• Êñ∞Â¢ûÔºöÂèëÈÄÅÁ≥ªÁªüÂÖ≥Èó≠‰∫§ÊòìÊ∂àÊÅØ
			         */
			        async sendSystemCloseMessage() {
			            try {
			                const systemMessage = 'Âõ†7Â§©‰πãÂÜÖÂçñÂÆ∂Ê≤°ÊúâÂêåÊÑèÔºåËØ•‰∫§ÊòìËá™Âä®ÂÖ≥Èó≠';
			                
			                const reqObj = {
			                    openid1: this.openid,
			                    openid2: this.userBase.openid !== this.goods_info.publisher_id 
			                            ? this.goods_info.publisher_id 
			                            : this.goods_info.other_openid,
			                    goods_id: this.goods_info.goods_id,
			                    senderid: 'system', // üî• Ê†áËÆ∞‰∏∫Á≥ªÁªüÊ∂àÊÅØ
			                    content: systemMessage,
			                    message_type: 'system',
			                };
			                
			                const { data: res } = await uni.$http.post('/chats/message', reqObj);
			                
			                if (res.meta.status === 200) {
			                    // Êõ¥Êñ∞Áä∂ÊÄÅ‰∏∫Êó†‰∫§Êòì
			                    this.transactionStatus = 0;
			                    this.isTransactionInitiator = false;
			                    
			                    // Âà∑Êñ∞Ê∂àÊÅØÂàóË°®
			                    const newMessages = this.processMessages(res.message.messages);
			                    this.messages = newMessages;
			                    
			                    if (newMessages.length > 0) {
			                        this.lastMessageId = newMessages[newMessages.length - 1].id || 
			                                            newMessages[newMessages.length - 1].created_at;
			                    }
			                    
			                    // ÊªöÂä®Âà∞ÊúÄÊñ∞Ê∂àÊÅØ
			                    this.$nextTick(() => {
			                        this.scrollToBottom();
			                    });
			                    
			                    console.log('‚úÖ Á≥ªÁªüÂÖ≥Èó≠‰∫§ÊòìÊ∂àÊÅØÂ∑≤ÂèëÈÄÅ');
			                }
			            } catch (error) {
			                console.error('‚ùå ÂèëÈÄÅÁ≥ªÁªüÊ∂àÊÅØÂ§±Ë¥•:', error);
			            }
			        },
					
			/**
			 * ‰π∞ÂÆ∂Á°ÆËÆ§‰∫§ÊòìÂÆåÊàê
			 */
			async finish() {
			    try {
			        // ‰∫åÊ¨°Á°ÆËÆ§
			        const [err, res] = await uni.showModal({
			            title: 'Á°ÆËÆ§‰∫§ÊòìÂÆåÊàê',
			            content: 'Á°ÆËÆ§Â∑≤ÂÆåÊàêÁ∫ø‰∏ã‰∫§ÊòìÔºüÁ°ÆËÆ§Âêé‰∫§ÊòìÁä∂ÊÄÅÂ∞ÜÂèò‰∏∫Â∑≤ÂÆåÊàê',
			            confirmText: 'Á°ÆËÆ§ÂÆåÊàê',
			            cancelText: 'ÂèñÊ∂à'
			        });
			        
			        if (err || !res.confirm) {
			            return;
			        }
			        
			        uni.showLoading({
			            title: 'Á°ÆËÆ§‰∏≠...',
			            mask: true
			        });
			        
			        // ÊûÑÂª∫ÂÆåÊàê‰∫§ÊòìÊ∂àÊÅØÂÜÖÂÆπ
			        const finishData = {
			            goods_id: this.goods_info.goods_id,
			            goods_name: this.goods_info.goods_name,
			            goods_price: this.goods_info.goods_price,
			            timestamp: Date.now()
			        };
			        
			        // ÂèëÈÄÅ"‰∫§ÊòìÂÆåÊàê"Á±ªÂûãÁöÑÊ∂àÊÅØ
			        const reqObj = {
			            openid1: this.openid,
			            openid2: this.userBase.openid !== this.goods_info.publisher_id 
			                    ? this.goods_info.publisher_id 
			                    : this.goods_info.other_openid,
			            goods_id: this.goods_info.goods_id,
			            senderid: this.openid,
			            content: JSON.stringify(finishData),
			            message_type: 'finish',  // üî• ‰∫§ÊòìÂÆåÊàêÊ∂àÊÅØÁ±ªÂûã
			        };
			        
			        const { data: res2 } = await uni.$http.post('/chats/message', reqObj);
			        
			        uni.hideLoading();
			        
			        if (res2.meta.status === 200) {
			            // Êõ¥Êñ∞Áä∂ÊÄÅ‰∏∫‰∫§ÊòìÂÆåÊàê
			            this.transactionStatus = 3;
			            
			            // Âà∑Êñ∞Ê∂àÊÅØÂàóË°®
			            const newMessages = this.processMessages(res2.message.messages);
			            this.messages = newMessages;
			            
			            if (newMessages.length > 0) {
			                this.lastMessageId = newMessages[newMessages.length - 1].id || 
			                                    newMessages[newMessages.length - 1].created_at;
			            }
			            
			            uni.showToast({
			                title: '‰∫§ÊòìÂ∑≤ÂÆåÊàê',
			                icon: 'success',
			                duration: 2000
			            });
			            
			            // ÊªöÂä®Âà∞ÊúÄÊñ∞Ê∂àÊÅØ
			            this.$nextTick(() => {
			                this.scrollToBottom();
			            });
			        } else {
			            uni.showToast({
			                title: res2.meta.msg || 'Êìç‰ΩúÂ§±Ë¥•',
			                icon: 'none'
			            });
			        }
			    } catch (error) {
			        uni.hideLoading();
			        console.error('Á°ÆËÆ§‰∫§ÊòìÂÆåÊàêÂ§±Ë¥•:', error);
			        uni.showToast({
			            title: 'Êìç‰ΩúÂ§±Ë¥•',
			            icon: 'none'
			        });
			    }
			},


			/**
			 * È¢ÑËßàË°®ÊÉÖÂåÖ(ÊîæÂ§ßÊü•Áúã)
			 */
			previewEmoji(emojiUrl) {
			    uni.previewImage({
			        current: emojiUrl,
			        urls: [emojiUrl]
			    });
			},
			
			 /**
			     * ÈÄâÊã©Ë°®ÊÉÖÂåÖÂèëÈÄÅ
			     */
			    async selectEmoji(emoji) {
			        try {
			            const reqObj = {
			                openid1: this.openid,
			                openid2: this.userBase.openid !== this.goods_info.publisher_id 
			                        ? this.goods_info.publisher_id 
