			                // sub_type: 'emoji'       // Ê†áËÆ∞‰∏∫Ë°®ÊÉÖÂåÖ
			            };
			            
			            const { data: res } = await uni.$http.post('/chats/message', reqObj);
			            
			            if (res.meta.status === 200) {
			                const newMessages = this.processMessages(res.message.messages);
