			    },
				
			/**
			 * Âä†ËΩΩÂΩìÂâçÂïÜÂìÅÁöÑËÆ¢ÂçïÁä∂ÊÄÅ
			 */
			async loadCurrentOrder() {
				try {
					// ÂÅáËÆæÂêéÁ´ØÊúâ‰∏Ä‰∏™Êé•Âè£ÂèØ‰ª•Êü•ËØ¢ÂΩìÂâçÁî®Êà∑ÂØπËØ•ÂïÜÂìÅÁöÑÊúÄÊñ∞ËÆ¢Âçï
					const { data: res } = await uni.$http.get('/orders/current', {
