					this.currentOrder = null;
				}
			},
			
			/**
			 * Á°ÆËÆ§Êî∂Ë¥ß
			 */
			async confirmReceipt() {
				// ‰∫åÊ¨°Á°ÆËÆ§
				const [err, res] = await uni.showModal({
					title: 'Á°ÆËÆ§Êî∂Ë¥ß',
					content: 'Á°ÆËÆ§Â∑≤Êî∂Âà∞ÂïÜÂìÅ?Êî∂Ë¥ßÂêéÊ¨æÈ°πÂ∞ÜËΩ¨ÂÖ•ÂçñÂÆ∂Ë¥¶Êà∑',
					confirmText: 'Á°ÆËÆ§Êî∂Ë¥ß',
					cancelText: 'ÂèñÊ∂à'
				});
				
				if (err || !res.confirm) {
					return;
				}
				
				try {
					uni.showLoading({
						title: 'Â§ÑÁêÜ‰∏≠...',
						mask: true
					});
					
					const { data: result } = await uni.$http.post('/orders/confirm_receipt', {
						order_number: this.currentOrder.order_number,
						openid: this.userBase.openid
					});
					
					uni.hideLoading();
					
					if (result.meta.status === 200) {
						uni.showToast({
							title: 'Á°ÆËÆ§Êî∂Ë¥ßÊàêÂäü',
							icon: 'success',
							duration: 2000
						});
						
						// Êõ¥Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
						this.currentOrder.order_status = 3;
						
						// ÂèëÈÄÅÁ°ÆËÆ§Êî∂Ë¥ßÊ∂àÊÅØ
						await this.sendPaymentMessage('ÊàëÂ∑≤Á°ÆËÆ§Êî∂Ë¥ß,‰∫§ÊòìÂÆåÊàê!');
						
						// Âà∑Êñ∞Ê∂àÊÅØÂàóË°®
						await this.loadMessages();
					} else {
						uni.showToast({
							title: result.meta.msg || 'Á°ÆËÆ§Êî∂Ë¥ßÂ§±Ë¥•',
							icon: 'none',
							duration: 2000
						});
					}
				} catch (error) {
					uni.hideLoading();
					console.error('‚ùå Á°ÆËÆ§Êî∂Ë¥ßÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'Á°ÆËÆ§Êî∂Ë¥ßÂ§±Ë¥•,ËØ∑ÈáçËØï',
						icon: 'none',
						duration: 2000
					});
				}
			},
			
			/**
			 * üî• ‰øÆÊîπÔºöÂä†ËΩΩÊ∂àÊÅØÊó∂Ê£ÄÊü•ÊòØÂê¶ÊúâÊñ∞Ê∂àÊÅØ
			 */
			async loadMessages(silent = false) {
			  if (this.isRequesting) return;
			  
			  this.isRequesting = true;
			  
			  try {
			    const req = {
			      openid1: this.openid,
			      openid2: this.userBase.openid !== this.goods_info.publisher_id 
			              ? this.goods_info.publisher_id 
			              : this.goods_info.other_openid,
			      goods_id: this.goods_info.goods_id,
			      _hideLoading: true
			    };
			    
			    const { data: res } = await uni.$http.get('/chats/message', req);
			    
			    if (res.meta.status === 200) {
			      const newMessages = this.processMessages(res.message.messages);
			      
			      if (newMessages.length > 0) {
			        const newLastMessageId = newMessages[newMessages.length - 1].id || 
			                                newMessages[newMessages.length - 1].created_at;
			        
			        // üî• Êñ∞Â¢ûÔºöÊ£ÄÊµãÊòØÂê¶ÊúâÊñ∞Ê∂àÊÅØ
			        if (this.lastMessageId !== newLastMessageId) {
			          const hadMessages = this.messages.length > 0;
			          const oldLength = this.messages.length;
			          
			          this.messages = newMessages;
			          this.lastMessageId = newLastMessageId;
			          
			          await this.checkTransactionExpiration();
			          
			          // üî• Êñ∞Â¢ûÔºöÂ§ÑÁêÜÊñ∞Ê∂àÊÅØÈÄªËæë
			          if (hadMessages && !silent) {
			            const newMessagesCount = newMessages.length - oldLength;
			            
			            // Ê£ÄÊü•ÊúÄÂêé‰∏ÄÊù°Ê∂àÊÅØÊòØÂê¶ÊòØÂà´‰∫∫ÂèëÁöÑ
			            const lastMessage = newMessages[newMessages.length - 1];
			            const isFromOther = lastMessage.openid !== this.openid && 
			                               lastMessage.message_type !== 'system';
			            
			            if (newMessagesCount > 0 && isFromOther) {
			              if (this.isNearBottom) {
			                // Â¶ÇÊûúÊé•ËøëÂ∫ïÈÉ®ÔºåËá™Âä®ÊªöÂä®
			                this.$nextTick(() => {
			                  this.scrollToBottom();
			                });
			              } else {
			                // Â¶ÇÊûú‰∏çÂú®Â∫ïÈÉ®ÔºåÊòæÁ§∫ÊèêÁ§∫
			                this.newMessageCount += newMessagesCount;
			                this.showNewMessageTip = true;
			              }
			            } else {
			              // Ëá™Â∑±ÂèëÁöÑÊ∂àÊÅØÔºåÁõ¥Êé•ÊªöÂä®Âà∞Â∫ïÈÉ®
			              this.$nextTick(() => {
			                this.scrollToBottom();
			              });
			            }
			          }
			        }
			      } else {
			        this.messages = newMessages;
			      }
			    }
			  } catch (error) {
			    console.error('Âä†ËΩΩÊ∂àÊÅØÂ§±Ë¥•:', error);
			  } finally {
			    this.isRequesting = false;
			  }
