			// ÂèëÈÄÅÊ∂àÊÅØ
			async sendMessage() {
				if (!this.inputText.trim()) {
					return;
				}
				
				const newMessage = this.inputText;
				this.inputText = '';
				
				try {
					// ÂèëÈÄÅÊ∂àÊÅØÂà∞ÂêéÁ´Ø
					const requyObj = {
						openid1: this.openid,
						openid2: this.userBase.openid !== this.goods_info.publisher_id 
								? this.goods_info.publisher_id 
								: this.goods_info.other_openid,
						goods_id: this.goods_info.goods_id,
						senderid: this.openid,
						content: newMessage,
						message_type: 'text',
					};
					
					const { data: res } = await uni.$http.post('/chats/message', requyObj)
					
					if (res.meta.status === 200) {
						// ÂèëÈÄÅÊàêÂäüÂêéÁ´ãÂç≥Âà∑Êñ∞Ê∂àÊÅØÂàóË°®
						const newMessages = this.processMessages(res.message.messages);
						this.messages = newMessages;
						
						// Êõ¥Êñ∞ÊúÄÂêé‰∏ÄÊù°Ê∂àÊÅØID
						if (newMessages.length > 0) {
							this.lastMessageId = newMessages[newMessages.length - 1].id || 
												newMessages[newMessages.length - 1].created_at;
						}
						
						// ÊªöÂä®Âà∞Â∫ïÈÉ®
						this.$nextTick(() => {
							this.scrollToBottom();
						});
					}
				} catch (error) {
					console.error('ÂèëÈÄÅÊ∂àÊÅØÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'ÂèëÈÄÅÂ§±Ë¥•',
						icon: 'none'
					});
				}
			},
			
			showEmojiPicker() {
			        this.showEmojiPanel = !this.showEmojiPanel;
			    },
			
			   /**
			     * ÊòæÁ§∫Ê∑ªÂä†ËèúÂçï - ‰øÆÊîπÂéüÊñπÊ≥ï
			     */
			    showAddMenu() {
			        uni.showActionSheet({
			            itemList: ['‰ªéÁõ∏ÂÜåÈÄâÊã©', 'ÊãçÁÖß'],
			            success: (res) => {
			                if (res.tapIndex === 0) {
			                    this.chooseImageFromAlbum();
			                } else if (res.tapIndex === 1) {
			                    this.chooseImageFromCamera();
			                }
			            }
			        });
			    },
				 /**
				     * ‰ªéÁõ∏ÂÜåÈÄâÊã©ÂõæÁâá
				     */
				    chooseImageFromAlbum() {
				        uni.chooseImage({
				            count: 1,
				            sizeType: ['compressed'],
				            sourceType: ['album'],
				            success: (res) => {
				                this.uploadAndSendImage(res.tempFilePaths);
				            }
				        });
				    },
					
					 /**
					     * ÊãçÁÖß
					     */
					    chooseImageFromCamera() {
					        uni.chooseImage({
					            count: 1,
					            sizeType: ['compressed'],
					            sourceType: ['camera'],
					            success: (res) => {
					                this.uploadAndSendImage(res.tempFilePaths);
					            }
					        });
					    },
						
						/**
						     * ‰∏ä‰º†Âπ∂ÂèëÈÄÅÂõæÁâá
						     */
						    async uploadAndSendImage(filePaths) {
						        if (!filePaths || filePaths.length === 0) {
						            return;
						        }
						        
						        this.uploading = true;
						        uni.showLoading({
						            title: '‰∏ä‰º†‰∏≠...',
						            mask: true
						        });
						        
						        try {
						            const filePath = filePaths[0];
						            
						            // 1. Ëé∑Âèñ‰∏ä‰º†Âá≠ËØÅ
						            const { data: tokenRes } = await uni.$http.get('/upload/token', {
						                openid: this.openid,
						                fileType: 'image'
						            });
						            
						            if (tokenRes.meta.status !== 200) {
						                throw new Error('Ëé∑Âèñ‰∏ä‰º†Âá≠ËØÅÂ§±Ë¥•');
						            }
						            
						            // 2. ËØªÂèñÊñá‰ª∂ÂÜÖÂÆπ
						            const fileContent = await new Promise((resolve, reject) => {
						                uni.getFileSystemManager().readFile({
						                    filePath: filePath,
						                    encoding: 'base64',
						                    success: (res) => resolve(res.data),
						                    fail: reject
						                });
						            });
						            
						            // 3. ‰∏ä‰º†Âà∞OSS
						            await new Promise((resolve, reject) => {
						                uni.request({
						                    url: tokenRes.message.publicUrl,
						                    method: 'PUT',
						                    header: {
						                        'Content-Type': 'application/octet-stream'
						                    },
						                    data: uni.base64ToArrayBuffer(fileContent),
						                    success: (res) => {
						                        if (res.statusCode === 200) {
						                            resolve(res);
						                        } else {
						                            reject(new Error(`‰∏ä‰º†Â§±Ë¥•: ${res.statusCode}`));
						                        }
						                    },
						                    fail: reject
						                });
						            });
						            
						            const imageUrl = tokenRes.message.publicUrl;
						            
						            uni.hideLoading();
						            
						            // 4. ÂèëÈÄÅÂõæÁâáÊ∂àÊÅØ
						            const reqObj = {
						                openid1: this.openid,
						                openid2: this.userBase.openid !== this.goods_info.publisher_id 
						                        ? this.goods_info.publisher_id 
						                        : this.goods_info.other_openid,
						                goods_id: this.goods_info.goods_id,
						                senderid: this.openid,
						                content: imageUrl,
						                message_type: 'image',  // Ê†áËÆ∞‰∏∫ÂõæÁâáÊ∂àÊÅØ
						                sub_type: 'image'       // Ê†áËÆ∞‰∏∫ÊôÆÈÄöÂõæÁâá
						            };
						            
						            const { data: res } = await uni.$http.post('/chats/message', reqObj);
						            
						            if (res.meta.status === 200) {
						                const newMessages = this.processMessages(res.message.messages);
						                this.messages = newMessages;
						                
						                if (newMessages.length > 0) {
						                    this.lastMessageId = newMessages[newMessages.length - 1].id || 
						                                        newMessages[newMessages.length - 1].created_at;
						                }
						                
						                this.$nextTick(() => {
						                    this.scrollToBottom();
						                });
						            }
						            
						        } catch (error) {
						            console.error('‚ùå ‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•:', error);
						            uni.hideLoading();
						            uni.showToast({
						                title: error.message || '‰∏ä‰º†Â§±Ë¥•',
						                icon: 'none',
						                duration: 2000
						            });
						        } finally {
						            this.uploading = false;
						        }
						    },
							
							/**
							     * È¢ÑËßàÂõæÁâá
							     */
							    previewImage(imageUrl) {
							        // Êî∂ÈõÜÊâÄÊúâÂõæÁâáÊ∂àÊÅØÁöÑURL
							        const imageUrls = this.messages
							            .filter(msg => msg.message_type === 'image')
							            .map(msg => msg.content);
							        
							        uni.previewImage({
							            current: imageUrl,
							            urls: imageUrls
							        });
							    },
								
					
			
			// ÊªöÂä®Âà∞Â∫ïÈÉ®
			scrollToBottom() {
				if (this.messages.length > 0) {
					this.scrollIntoView = 'msg-' + (this.messages.length - 1);
				}
			},
			
			// Ê†ºÂºèÂåñÊó∂Èó¥
			formatTime(date) {
				const month = String(date.getMonth() + 1).padStart(2, '0');
				const day = String(date.getDate()).padStart(2, '0');
				const hour = String(date.getHours()).padStart(2, '0');
				const minute = String(date.getMinutes()).padStart(2, '0');
				return `${month}-${day} ${hour}:${minute}`;
			},
			
			/**
			         * üî• ‰øÆÊîπÔºöÂ§ÑÁêÜÊ∂àÊÅØÊó∂ËØÜÂà´Á≥ªÁªüÊ∂àÊÅØ
			         */
			        processMessages(messages) {
			            if (!Array.isArray(messages)) {
			                console.log("messages‰∏çÊòØÊï∞ÁªÑ");
			                return [];
			            }
			            
			            // Ê£ÄÊü•ÊòØÂê¶ÊúâÁ≥ªÁªüÂÖ≥Èó≠Ê∂àÊÅØ
			            const hasSystemCloseMsg = messages.some(msg => 
			                msg.message_type === 'system' && 
			                msg.content && 
			                msg.content.includes('‰∫§ÊòìËá™Âä®ÂÖ≥Èó≠')
			            );
			            
			            // üî• Â¶ÇÊûúÊúâÁ≥ªÁªüÂÖ≥Èó≠Ê∂àÊÅØÔºåÈáçÁΩÆ‰∫§ÊòìÁä∂ÊÄÅ
			            if (hasSystemCloseMsg) {
			                this.transactionStatus = 0;
			                this.isTransactionInitiator = false;
			            } else {
			                // ÂéüÊúâÁöÑÁä∂ÊÄÅÊ£ÄÊü•ÈÄªËæë
			                const hasTransactionMessage = messages.some(msg => msg.message_type === 'transaction');
			                const hasAgreeMessage = messages.some(msg => msg.message_type === 'agree');
			                const hasFinishMessage = messages.some(msg => msg.message_type === 'finish');
			                
			                if (hasFinishMessage) {
			                    this.transactionStatus = 3;
			                } else if (hasAgreeMessage) {
			                    this.transactionStatus = 2;
			                } else if (hasTransactionMessage) {
			                    const latestTransaction = [...messages]
			                        .reverse()
			                        .find(msg => msg.message_type === 'transaction');
			                    
			                    if (latestTransaction) {
			                        if (latestTransaction.openid === this.openid) {
			                            this.transactionStatus = 1;
			                            this.isTransactionInitiator = true;
			                        } else {
			                            this.transactionStatus = 1;
			                            this.isTransactionInitiator = false;
			                        }
			                    }
			                }
			            }
			            
			            return messages.map(message => {
			                const processed = {
			                    ...message,
			                    type: message.openid === this.openid ? 'sent' : 
			                          message.message_type === 'system' ? 'system' : 'received', // üî• Á≥ªÁªüÊ∂àÊÅØÊ†áËÆ∞
			                    is_payment: message.is_payment || 0,
			                    message_type: message.message_type || 'text',
			                    sub_type: message.sub_type || null
			                };
			                
			                // ‰∫§ÊòìÊ∂àÊÅØÂ§ÑÁêÜ
			                if (message.message_type === 'transaction') {
			                    try {
			                        const transactionData = JSON.parse(message.content);
			                        processed.location = transactionData.location || message.location;
			                        processed.transactionData = transactionData;
			                    } catch (e) {
			                        console.error('Ëß£Êûê‰∫§ÊòìÊ∂àÊÅØÂ§±Ë¥•:', e);
			                        processed.location = message.location;
			                    }
			                }
			                
			                // ÂêåÊÑèÊ∂àÊÅØÂ§ÑÁêÜ
			                if (message.message_type === 'agree') {
			                    try {
			                        const agreeData = JSON.parse(message.content);
			                        processed.agreeData = agreeData;
			                    } catch (e) {
			                        console.error('Ëß£ÊûêÂêåÊÑèÊ∂àÊÅØÂ§±Ë¥•:', e);
			                    }
			                }
			                
			                // ÂÆåÊàêÊ∂àÊÅØÂ§ÑÁêÜ
			                if (message.message_type === 'finish') {
			                    try {
			                        const finishData = JSON.parse(message.content);
			                        processed.finishData = finishData;
			                    } catch (e) {
			                        console.error('Ëß£ÊûêÂÆåÊàêÊ∂àÊÅØÂ§±Ë¥•:', e);
			                    }
			                }
			                
			                return processed;
			            });
			        },
			
		// Âú® methods ‰∏≠‰øÆÊîπ agreeTransaction ÊñπÊ≥ï
		async agreeTransaction() {
		    try {
		        uni.showLoading({
		            title: 'Á°ÆËÆ§‰∏≠...',
		            mask: true
		        });
		        
		        // üî• ÊûÑÂª∫ÂêåÊÑè‰∫§ÊòìÊ∂àÊÅØÂÜÖÂÆπ
		        const agreeData = {
		            goods_id: this.goods_info.goods_id,
		            goods_name: this.goods_info.goods_name,
		            goods_price: this.goods_info.goods_price,
		            timestamp: Date.now()
		        };
		        
		        // üî• ÂèëÈÄÅ"ÂêåÊÑè‰∫§Êòì"Á±ªÂûãÁöÑÊ∂àÊÅØ
		        const reqObj = {
		            openid1: this.openid,
		            openid2: this.userBase.openid !== this.goods_info.publisher_id 
		                    ? this.goods_info.publisher_id 
		                    : this.goods_info.other_openid,
		            goods_id: this.goods_info.goods_id,
		            senderid: this.openid,
		            content: JSON.stringify(agreeData),
		            message_type: 'agree',  // üî• Êñ∞Â¢û: ÂêåÊÑè‰∫§ÊòìÊ∂àÊÅØÁ±ªÂûã
		        };
		        
		        const { data: res } = await uni.$http.post('/chats/message', reqObj);
		        
		        uni.hideLoading();
		        
		        if (res.meta.status === 200) {
		            // Êõ¥Êñ∞Áä∂ÊÄÅ‰∏∫‰∫§ÊòìËøõË°å‰∏≠
		            this.transactionStatus = 2;
		            
		            // Âà∑Êñ∞Ê∂àÊÅØÂàóË°®
		            const newMessages = this.processMessages(res.message.messages);
		            this.messages = newMessages;
		            
		            if (newMessages.length > 0) {
		                this.lastMessageId = newMessages[newMessages.length - 1].id || 
		                                    newMessages[newMessages.length - 1].created_at;
		            }
		            
		            uni.showToast({
		                title: 'Â∑≤ÂêåÊÑè‰∫§Êòì',
		                icon: 'success'
		            });
		            
		            // ÊªöÂä®Âà∞ÊúÄÊñ∞Ê∂àÊÅØ
		            this.$nextTick(() => {
		                this.scrollToBottom();
		            });
		        } else {
		            uni.showToast({
		                title: res.meta.msg || 'Êìç‰ΩúÂ§±Ë¥•',
		                icon: 'none'
		            });
		        }
		    } catch (error) {
		        uni.hideLoading();
		        console.error('ÂêåÊÑè‰∫§ÊòìÂ§±Ë¥•:', error);
		        uni.showToast({
		            title: 'Êìç‰ΩúÂ§±Ë¥•',
		            icon: 'none'
		        });
		    }
		},
			
			
			// Ë∑≥ËΩ¨Âà∞ÂïÜÂìÅËØ¶ÊÉÖ
			async gotoDetail(item) {
				try {
					// 1. ÂÖàÂ¢ûÂä†ÊµèËßàÊ¨°Êï∞
					console.log('üìä Â¢ûÂä†ÂïÜÂìÅÊµèËßàÊ¨°Êï∞:', item.goods_id);
					const { data: res } = await uni.$http.post('/goods/view', {
						goods_id: item.goods_id,
						// ÂèØÈÄâÔºöÊ∑ªÂä†Áî®Êà∑‰ø°ÊÅØÁî®‰∫éÁªüËÆ°ÂàÜÊûê
						user_openid: this.openid || null
					});
				
					if (res.meta.status === 200) {
						console.log('‚úÖ ÊµèËßàÊ¨°Êï∞Â¢ûÂä†ÊàêÂäü');
					} else {
						console.warn('‚ö†Ô∏è ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Â§±Ë¥•:', res.meta.msg);
					}
				} catch (error) {
					console.error('‚ùå ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Êé•Âè£ÈîôËØØ:', error);
					// Âç≥‰ΩøÊé•Âè£Â§±Ë¥•‰πüÁªßÁª≠Ë∑≥ËΩ¨Ôºå‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™å
				}
				uni.navigateTo({
					url: '/subpkg/goods_detail/goods_detail?goods_id=' + item.goods_id
				})
			},
		},
		computed: {
			...mapState('m_user', ['token', 'code', 'userBase', 'openid', 'address']),
			 // üî• Êñ∞Â¢ûÔºöÂ§ÑÁêÜÂïÜÂìÅÂêçÁß∞Êà™Êñ≠
			    displayGoodsName() {
			        if (!this.goods_info || !this.goods_info.goods_name) {
			            return '';
			        }
			        const name = this.goods_info.goods_name;
			        return name.length > 15 ? name.substring(0, 13) + '...' : name;
			    },
				
			// ÊòæÁ§∫Â∑≤‰øùÂ≠òÁöÑÂú∞ÂùÄÊñáÊú¨
			savedAddressText() {
				if (this.address && this.address.fullAddress) {
					return this.address.fullAddress;
				}
				return '';
			}
		}
	}
</script>

