.new-message-tip {
	position: fixed;
	right: 32rpx;
	bottom: 180rpx;
	z-index: 99;
	opacity: 0;
	transform: translateY(20rpx) scale(0.8);
	transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
	pointer-events: none;
	
	&.show {
		opacity: 1;
		transform: translateY(0) scale(1);
		pointer-events: auto;
	}
	
	.tip-content {
		display: flex;
		align-items: center;
		padding: 16rpx 24rpx;
		background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
		color: #ffffff;
		border-radius: 40rpx;
		box-shadow: 0 8rpx 24rpx rgba(102, 126, 234, 0.4);
		animation: tipPulse 2s infinite;
		
		.tip-icon {
			font-size: 32rpx;
			margin-right: 8rpx;
		}
		
		.tip-text {
			font-size: 26rpx;
			font-weight: 500;
			margin-right: 8rpx;
		}
		
		.tip-arrow {
			font-size: 28rpx;
			animation: bounce 1s infinite;
		}
	}
}

@keyframes tipPulse {
	0%, 100% {
		box-shadow: 0 8rpx 24rpx rgba(102, 126, 234, 0.4);
	}
	50% {
		box-shadow: 0 8rpx 32rpx rgba(102, 126, 234, 0.6);
	}
}

@keyframes bounce {
	0%, 100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(4rpx);
	}
}

